<template>
    <div>
    	<el-input type="email"
    		:placeholder="currentField.isPlaceholderVisible ? currentField.placeholder : ''">
    	</el-input>
    </div>
</template>

<script>
  export default {
    name: 'Email',
    props: ['currentField']
  }
</script>



