<template>
	<div>
		<el-form label-position="left">
			<el-collapse v-model="activeCollapse" accordion>
				<!-- Appearance -->
				<el-collapse-item title="Основные" name="appearance">

				<el-form-item label="Отступ формы">
					<el-input-number v-model="themingVars.dragpadding2" :min="0" :max="50"></el-input-number>
				</el-form-item>

					<el-form-item label="Цвет формы">
						<el-color-picker v-model="themingVars.dragAreaRahmetBG"></el-color-picker>
					</el-form-item>


			  	</el-collapse-item>




			</el-collapse>
		</el-form>
	</div>
</template>

<script>
	export default {
		name: 'ThemingRahmet',
		store: ['themingVars'],
		data(){
			return {
				fileList: [],
				activeCollapse: ['appearance'],
				fontOptions: [{
		          	label: 'Sans Serif',
		          	options: [{
		            	value: 'Arial',
		            	label: 'Arial'
		          	}, {
		            	value: 'Arial Black',
		            	label: 'Arial Black'
		          	}, {
		            	value: 'Tahoma',
		            	label: 'Tahoma'
		          	}, {
		            	value: 'Trebuchet MS',
		            	label: 'Trebuchet MS'
		          	}, {
		            	value: 'Verdana',
		            	label: 'Verdana'
		          	}]
		        }, {
		          	label: 'Serif ',
		          	options: [{
		            	value: 'Georgia',
		            	label: 'Georgia'
		          	}, {
		            	value: 'Times',
		            	label: 'Times'
		          	}, {
		            	value: 'Times New Roman',
		            	label: 'Times New Roman'
		          	}]
		        }, {
		          	label: 'Monospace',
		          	options: [{
		            	value: 'Courier',
		            	label: 'Courier'
		          	}, {
		            	value: 'Courier New',
		            	label: 'Courier New'
		          	}]
		        }]
			}
		},
		methods: {
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			backGroundSave(file, fileList) {
				this.backgroundFrom = fileList.url
				console.log('backgroundFrom',fileList.url);
			}
		}
	}
</script>



