<template>
	<div>
		<el-form label-position="left">
			<el-collapse v-model="activeCollapse" accordion>
				<!-- Appearance -->
				<el-collapse-item title="Основные" name="appearance">

					<el-form-item label="Отступ формы">
						<el-input-number v-model="themingVars.dragpadding" :min="0" :max="50"></el-input-number>
					</el-form-item>


					<el-form-item label="Цвет формы">
						<el-color-picker v-model="themingVars.dragAreaBG"></el-color-picker>
					</el-form-item>
					<el-form-item label="Картинка формы">

						<el-upload
						class="upload-demo"
						action="https://jsonplaceholder.typicode.com/posts/"
						:on-preview="handlePreview"
						:on-remove="handleRemove"
						:limit="1"
						:on-success="backGroundSave"
						:file-list="fileList"
						list-type="picture">
						<div v-show="this.fileList.length == 0" class="bbbtuns">
							<el-button  size="small" type="primary">Загрузить</el-button>
						</div>
					</el-upload>
				</el-form-item>






					<el-form-item label="Шрифт">
					    <el-select v-model="themingVars.globalFontFamily" placeholder="Select font family">
					    	<el-option-group
						      v-for="group in fontOptions"
						      :key="group.label"
						      :label="group.label">
						      <el-option
						        v-for="item in group.options"
						        :key="item.value"
						        :label="item.label"
						        :value="item.value">
						      </el-option>
						    </el-option-group>
					    </el-select>
					</el-form-item>

					<el-form-item label="Размер шрифта">
			    		<el-input-number v-model="themingVars.globalFontSize" :min="0" :max="50"></el-input-number>
			  		</el-form-item>

			  		<el-form-item label="Цвет текста">
			    		<el-color-picker v-model="themingVars.globalFontColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Цвет ссылки">
			    		<el-color-picker v-model="themingVars.globalLinkColor"></el-color-picker>
			  		</el-form-item>
			  	</el-collapse-item>

				<!-- Inputs -->
			  	<el-collapse-item title="Поля ввода" name="inputs">
			  		<el-form-item label="Закругление краев">
			    		<el-input-number v-model="themingVars.inputBorderRadius" :min="0" :max="50"></el-input-number>
			  		</el-form-item>

			    	<el-form-item label="Цвет рамки">
			    		<el-color-picker v-model="themingVars.inputBorderColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Цвет рамки при наведении">
			    		<el-color-picker v-model="themingVars.inputHoverBorderColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Цвет рамки при активном фокусе">
			    		<el-color-picker v-model="themingVars.inputFocusBorderColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Тень текста">
			    		<el-color-picker v-model="themingVars.inputShadowColor"></el-color-picker>
			  		</el-form-item>
			  	</el-collapse-item>

				<!-- Labels -->
			  	<el-collapse-item title="Наименование полей" name="labels">
			    	<el-form-item label="Цвет">
			    		<el-color-picker v-model="themingVars.primaryColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Жирность">
			    		<el-radio-group v-model="themingVars.labelFontWeight">
					      <el-radio-button :label="500">Нормальный</el-radio-button>
					      <el-radio-button :label="700">Жирный</el-radio-button>
					    </el-radio-group>
			  		</el-form-item>

			  		<el-form-item label="Размер шрифта">
			    		<el-input-number v-model="themingVars.labelFontSize" :min="1" :max="50"></el-input-number>
			  		</el-form-item>

			  		<el-form-item label="Отступ вниз">
			    		<el-input-number v-model="themingVars.labelMarginBottom" :min="0" :max="50"></el-input-number>
			  		</el-form-item>
			  	</el-collapse-item>

			  	<!-- Help block -->
			  	<el-collapse-item title="Блок текста под элементами" name="helptext">
			    	<el-form-item label="Цвет">
			    		<el-color-picker v-model="themingVars.helpTextColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Размер шрифта">
			    		<el-input-number v-model="themingVars.helpTextFontSize" :min="1" :max="50"></el-input-number>
			  		</el-form-item>

			  		<el-form-item label="Отступ вверх">
			    		<el-input-number v-model="themingVars.helpTextMarginTop" :min="0" :max="50"></el-input-number>
			  		</el-form-item>
			  	</el-collapse-item>

			  	<!-- Buttons -->
			  	<el-collapse-item title="Кнопка" name="buttons">
			    	<el-form-item label="Цвет заливки">
			    		<el-color-picker v-model="themingVars.buttonBackground"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Цвет рамки">
			    		<el-color-picker v-model="themingVars.buttonBorderColor"></el-color-picker>
			  		</el-form-item>

			  		<el-form-item label="Цвет">
			    		<el-color-picker v-model="themingVars.buttonColor"></el-color-picker>
			  		</el-form-item>
			  	</el-collapse-item>
			</el-collapse>
		</el-form>
	</div>
</template>

<script>
	export default {
		name: 'Theming',
		store: ['themingVars'],
		data(){
			return {
				fileList: [],
				activeCollapse: ['appearance'],
				fontOptions: [{
		          	label: 'Sans Serif',
		          	options: [{
		            	value: 'Arial',
		            	label: 'Arial'
		          	}, {
		            	value: 'Arial Black',
		            	label: 'Arial Black'
		          	}, {
		            	value: 'Tahoma',
		            	label: 'Tahoma'
		          	}, {
		            	value: 'Trebuchet MS',
		            	label: 'Trebuchet MS'
		          	}, {
		            	value: 'Verdana',
		            	label: 'Verdana'
		          	}]
		        }, {
		          	label: 'Serif ',
		          	options: [{
		            	value: 'Georgia',
		            	label: 'Georgia'
		          	}, {
		            	value: 'Times',
		            	label: 'Times'
		          	}, {
		            	value: 'Times New Roman',
		            	label: 'Times New Roman'
		          	}]
		        }, {
		          	label: 'Monospace',
		          	options: [{
		            	value: 'Courier',
		            	label: 'Courier'
		          	}, {
		            	value: 'Courier New',
		            	label: 'Courier New'
		          	}]
		        }]
			}
		},
		methods: {
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			backGroundSave(file, fileList) {
				this.backgroundFrom = fileList.url
				console.log('backgroundFrom',fileList.url);
			}
		}
	}
</script>



