<template>
  <div class="el-tabs__inner">
    <el-row :gutter="10" class="row-bg" >
      <draggable :list="fields"
                 :clone="clone"
                 class="dragArea"
                 :options="dropElementOptions"
                 @start="onStart">
        <el-col :span="12"
                :class="{ 'is-disabled': checkStopDragCondition(field) }"
                v-for="(field, index) in fields"
                :key="index">
          <el-button class="button__sidebar" :id="field.name"
                     type="info">
                     <span v-if="field.name == 'Fam'"></span>
                     {{ field.text }}
          </el-button>
        </el-col>
      </draggable>
    </el-row>
    <div>




    </div>
  </div>

</template>

<script>
  import { FormBuilder } from '@/components/form_elements/formbuilder'
  import draggable       from 'vuedraggable'
  import axios from 'axios'

  export default {
    name: 'Elements',
    store: ['forms', 'activeForm', 'backgroundFrom'],
    components: { draggable },
    data() {
      return {

        fields: FormBuilder.$data.fields,
        dropElementOptions: FormBuilder.$data.dropElementOptions
      };
    },

    methods: {
      clone(field){
        var newField  = {
          fieldName: field.text,
          fieldType: field.name,
          isUnique: field.isUnique
        }

        // Show placeholder
        if (field.isPlaceholderVisible){
          newField ["isPlaceholderVisible"] = false;
          newField ["placeholder"] = 'Текст до ввода...';
        }

        // Decide whether display label, required field, helpblock
        if (field.group == "form"){

          newField ["label"] = "Введите метку поля";

          if (field.name == "Fam"){
            newField ["label"] = "Введите фамилию";
          }
          if (field.name == "Fax"){
            newField ["label"] = "Введите факс";
          }
          if (field.name == "Tel"){
            newField ["label"] = "Введите телефон";
          }
          if (field.name == "Url"){
            newField ["label"] = "Вставьте ссылку";
          }

          newField ["isHelpBlockVisible"] =  false;
          newField ["helpBlockText"] = 'Введите текст помощи...';
          newField ["isRequired"] = false;
        }

        if (field.group == "button"){
          newField ["buttonText"] = "Отправить";
        }



        if (field.name == "TextEditor"){
          newField ["fieldText"] = "Полле текста...";
        }


        // Add dummy options for loading the radio/checkbox
        if (field.hasOptions){
          newField ["options"] = [
            { optionLabel: "Option 1", optionValue: "Option 1" },
            { optionLabel: "Option 2", optionValue: "Option 2" }
          ]
        }

        return newField;
      },
      onStart(){
        // console.log("start liao")
      },
      checkStopDragCondition(field){
        var form = this.forms,
            formArray = [];

        for (var key in form) {
          formArray.push(form[key]['fieldType'])
        }

        // Check if the fieldname exist in formArray
        // And when the field.isUnique too
        return _.includes(formArray, field.name) && field.isUnique;
      }
    }
  }
</script>

<style lang="scss" scoped>

.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .button__sidebar {
    width: 100%;
    margin-bottom: 10px;

    border: 1px solid #dddddd;
    border-radius: 0;
    background: white;

    .is-disabled & {
      opacity: 0.4;
    }
    &:hover, &:focus{
      color: Black;
      border: 1px solid #ffd43b;
    }


  }

  #Tel{
    background: white url('../../../src/assets/icon5.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }
  #Email{
    background: white url('../../../src/assets/icon1.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }
  #LongTextInput{
    background: white url('../../../src/assets/icon6.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }
  #Url{
    background: white url('../../../src/assets/icon4.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }
  #Fax{
    background: white url('../../../src/assets/icon2.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }
  #TextInput, #Fam{
    background: white url('../../../src/assets/icon3.png') no-repeat;
    background-position: 6px 50%;
    padding-left: 33px;
  }

  // Display this ghost in <main> only
  .wrapper--forms .sortable__ghost {
    position: relative;
    width: 100%;
    border-bottom: 2px solid black;
    margin-top: 2px;
    margin-bottom: 2px;

    [type="button"] {
      display: none;
    }

    &:before {
      content: "Перетащите сюда";
      background-color: black;
      color: white;
      position: absolute;
      left: 50%;
      font-size: 10px;
      border-radius: 10px;
      line-height: 15px;
      padding: 0 10px;
      top: -6px;
      transform: translateX(-50%);
    }
  }
</style>



