<template>
  <div>

<img src="../../../src/assets/pdd.png" class="img-responsive"/>

    <h3 class="head">Уведомлять о новой заполненной форме</h3>
    <div class="listchek">



      <el-checkbox v-model="notifi1" label="На почту" ></el-checkbox>
      <el-checkbox v-model="notifi2" label="СМС-уведомление" ></el-checkbox>
      <el-checkbox v-model="notifi3" label="Звонком на телефон" ></el-checkbox>


      <div class="phoneblok"   v-if="notifi3">
        <div  class="linephone" v-for="(item, index) in notifi3Phone">
          <el-input
          placeholder="Введите номер"
          v-model="item.phone"
          >
        </el-input>
        <el-button icon="el-icon-delete" @click="notifi3Phone.splice(index, 1)" ></el-button>
      </div>

    <div class="addphone" @click="addPhone">еще</div>
  </div>

  </div>
</div>
</template>

<script>
	export default {
		name: 'Notification',
    store: ['forms', 'activeForm', 'backgroundFrom', 'notifi1', 'notifi2', 'notifi3', 'notifi3Phone'],
		data(){
			return {

			}
		},
    methods:{
      addPhone(){
        this.notifi3Phone.push({
          id: this.notifi3Phone.length+1,
          phone: ''
        })
      }
    }
	}
</script>

<style lang="scss" scoped>
.phoneblok{
	.linephone{
		display: flex;
    margin-bottom: 6px;
    input{
      border-radius: 5px 0 0 5px;
    }
    button{
      border-radius: 0 5px 5px 0;
      border-left: 0;
    }
	}
}
.addphone{
  display: block;
  margin: 5px 10px 0 0;
  text-align: right;
  color: #cdcdcd;
  cursor: pointer;
}
</style>



