<template>
  <div class="main__wrapper">
    <el-container>
      <el-main :style="cssProps">

        <div class="wrapper--forms">
          <!-- Show empty state if empty -->
          <div v-if="forms.length == 0" class="emptyState">
            <span class="emptyState__emoji">  </span>


          </div>

<div class="dragAreaRahmet">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            v-model="rahmetsupl">
          </el-input>

</div>
        </div>


      </el-main>

      <el-aside class="wrapper--sidebar" width="450px">



        <el-tabs type="border-card" v-model="activeName">
          <el-tab-pane label="Свойства" name="first">  <ThemingRahmet/></el-tab-pane>
        </el-tabs>

      </el-aside>

    </el-container>


  </div>
</template>

<script>
  import { FormBuilder }  from '@/components/form_elements/formbuilder'

  export default {
    name: 'rahmet',

    store: ['forms', 'rahmetsupl', 'activeForm', 'activeTabForFields', 'themingVars'],

    data() {
      return {
        activeName: 'first',
        sortElementOptions: FormBuilder.$data.sortElementOptions
      };
    },
    computed: {


      img_section_style: function(){
        return {
          "background": 'url('+this.backgroundFrom.name+') no-repeat '
        }
      },

      cssProps() {
        // Return an object that will generate css properties key
        // to match with the themingVars
        //
        // Example output: { '--theme-primary-color': this.themingVars.primaryColor }
        var result = {},
            themingVars = this.themingVars;

        for (var v in themingVars) {
          if (themingVars.hasOwnProperty(v)) {
            var newV = "--theme-" + _.kebabCase(v),
                suffix = "";

            // Add px to the value if the default value contain 'px'
            if (_.includes(newV, 'size'))         suffix = "px"
            else if (_.includes(newV, 'margin'))  suffix = "px"
            else if (_.includes(newV, 'padding'))  suffix = "px"
            else if (_.includes(newV, 'radius'))  suffix = "px"

            result [newV] = themingVars[v] + suffix;
          }
        }

        console.log("result", result)

        return result;
      }
    },

    mounted(){
      console.log("form ->", this.forms)
      console.log("activeform ->", this.activeForm)
    },

    components: FormBuilder.$options.components,

    methods: {
      deleteElement(index){
        FormBuilder.deleteElement(index)
      },

      cloneElement(index, form){
        FormBuilder.cloneElement(index, form)
      },

      editElementProperties(form){
        FormBuilder.editElementProperties(form)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .main__wrapper {
    height: 100%;
  }



  .dragAreaRahmet {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    min-height: 10px;
    z-index: 2;
    height: 100%;
  }

    .form__selectedlabel {
      display: none;
      background: lighten(black, 20%);
      padding: 3px 5px;
      color: white;
      font-size: 10px;
      position: absolute;
      top: -17px;
      right: 15px;
    }

    .form__actionitem--move {
      position: absolute;
      right: -14px;
      top: 50%;
      transform: translateY(-50%);
      visibility: hidden;

      &:active, &:focus, &:hover {
        border-color: lighten(black, 50%);
        background: lighten(black, 50%);
      }
    }

    .form__actionlist {
      position: absolute;
      margin-top: 10px;
      visibility: hidden;
      z-index: 3;
      right: 0;
      box-shadow: 4px 4px 0 0 lighten(black, 80%);
      border-radius: 2px;
    }

    .form__group {
      margin-bottom: 25px;
      border: 2px solid transparent;
      position: relative;

      &:hover {
        border-color: lighten(black, 80%);

        .form__actionitem--move {
          visibility: visible;
        }
      }

      &.is--active {
        border-color: lighten(black, 50%);
        background: lighten(black, 95%);

        .form__actionlist { visibility: visible; }
        .form__selectedlabel { display: inline-block; }
      }
    }

</style>



