import Vue from 'vue';

import draggable        	from 'vuedraggable'


import TextInput 			from '@/components/form_elements/FormElementTextInput'
import Email 			from '@/components/form_elements/FormElementEmail'
import Fax 			from '@/components/form_elements/FormElementFaks'
import Url 			from '@/components/form_elements/FormElementUrl'
import Fam 			from '@/components/form_elements/FormElementFam'
import Tel 			from '@/components/form_elements/FormElementTel'

import LongTextInput 		from '@/components/form_elements/FormElementLongTextInput'
import NumberInput 			from '@/components/form_elements/FormElementNumberInput'
import SelectList 			from '@/components/form_elements/FormElementSelectList'
import RadioButton 			from '@/components/form_elements/FormElementRadioButton'
import Checkbox 			from '@/components/form_elements/FormElementCheckbox'
//import TimePicker 			from '@/components/form_elements/FormElementTimePicker'
//import DatePicker 			from '@/components/form_elements/FormElementDatePicker'
//import DatetimePicker 		from '@/components/form_elements/FormElementDatetimePicker'
//import Rating 				from '@/components/form_elements/FormElementRating'
import Button 				from '@/components/form_elements/FormElementButton'
//import TextEditor			from '@/components/form_elements/FormElementTextEditor'

import Elements         	from '@/components/form_elements/Elements'
import Properties			from '@/components/form_elements/Properties'
import Theming	 		 	from '@/components/form_elements/Theming'
import Notification	 		 	from '@/components/form_elements/Notification'
import ThemingRahmet	 		 	from '@/components/form_elements/ThemingRahmet'


export const FormBuilder = new Vue({
	components: { Elements, Properties, Theming, ThemingRahmet, Notification, draggable, Email, Fax, Tel, Fam, Url, TextInput, LongTextInput, NumberInput, SelectList, RadioButton, Checkbox, Button },
	data() {
	    return {
	      	fields: [
						{
	          		'name': 'Tel',
	          		'text': 'Телефон',
	          		'group': 'form', //form group
	          		'hasOptions': false,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': true,
	          		'isUnique': true
		        },
						{
								'name': 'Fax',
								'text': 'Факс',
								'group': 'form', //form group
								'hasOptions': false,
								'isRequired': false,
								'isHelpBlockVisible': false,
								'isPlaceholderVisible': true,
								'isUnique': true
						},
						{
								'name': 'TextInput',
								'text': 'Имя',
								'group': 'form', //form group
								'hasOptions': false,
								'isRequired': false,
								'isHelpBlockVisible': false,
								'isPlaceholderVisible': true,
								'isUnique': true
						},
						{
								'name': 'Fam',
								'text': 'Фамилия',
								'group': 'form', //form group
								'hasOptions': false,
								'isRequired': false,
								'isHelpBlockVisible': false,
								'isPlaceholderVisible': true,
								'isUnique': true
						},
						{
								'name': 'Url',
								'text': 'url',
								'group': 'form', //form group
								'hasOptions': false,
								'isRequired': false,
								'isHelpBlockVisible': false,
								'isPlaceholderVisible': true,
								'isUnique': true
						},
						{
	          		'name': 'Email',
	          		'text': 'Эл. адрес',
	          		'group': 'form', //form group
	          		'hasOptions': false,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': true,
	          		'isUnique': true
		        },

		        {
	          		'name': 'LongTextInput',
	          		'text': 'Комментарий',
	          		'group': 'form',
	          		'hasOptions': false,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': true,
	          		'isUnique': true
		        },

				{
	          		'name': 'SelectList',
	          		'text': 'Select',
	          		'group': 'form',
	          		'hasOptions': true,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': false,
	          		'isUnique': false
		      	},
		      	{
	          		'name': 'RadioButton',
	          		'text': 'Radio',
	          		'group': 'form',
	          		'hasOptions': true,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': false,
	          		'isUnique': false
		      	},
		      	{
	          		'name': 'Checkbox',
	          		'text': 'Checkbox',
	          		'group': 'form',
	          		'hasOptions': true,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': false,
	          		'isUnique': false
		      	},

		      	{
	          		'name': 'Button',
	          		'text': 'Отправить',
	          		'group': 'button',
	          		'hasOptions': false,
	          		'isRequired': false,
	          		'isHelpBlockVisible': false,
	          		'isPlaceholderVisible': false,
	          		'isUnique': true
		      	}
	      	],

	      	sortElementOptions: {
	          group: { name:'formbuilder', pull:false, put:true },
	          sort: true,
	          handle: ".form__actionitem--move"
	        },

	        dropElementOptions: {
	          group: { name:'formbuilder', pull:'clone', put:false },
	          sort: false,
	          ghostClass: "sortable__ghost",
	          filter: ".is-disabled"
	        }
	    }
	},
	methods: {
		deleteElement(index){
	        vm.$store.activeForm = [];
	        vm.$store.activeTabForFields = "elements";
	        this.$delete(vm.$store.forms, index);
	    },

	    cloneElement(index, form){
	    	var cloned = _.cloneDeep(form) // clone deep lodash
	        vm.$store.forms.splice(index, 0, cloned)
	    },

	    editElementProperties(form){
	    	vm.$store.activeForm = form;
        	vm.$store.activeTabForFields = "properties";
	    }
	}
});



